<template>
  <div id="userBar" class="show" :style="{left:data}">
    <a class="row-user" @click="jump();back()">
      <div class="icon">
        <img v-show="$root.bImg2" :src="$root.user.icon" alt="" class="cover">
        <img v-show="$root.bImg1" src="//static.missevan.com/assets/images/home/default-icon.jpg" alt="" class="cover">
      </div>
      <div class="details">
        <span class="username">
          <p v-show="$root.bNickname2">{{$root.user.nikename}}</p> 
          <p v-show="$root.bNickname1"></p>
          <span class="gt">&gt;</span>
        </span>
        <span class="info">
            <p class="dimond " v-show="$root.bImg2">: {{$root.user.fans}}</p>
            <p class="fish" v-show="$root.bNickname2">: {{$root.user.follow}}</p>
        </span>
      </div>
    </a>
    <a href="javascript:;" class="station">
      <i class="home icon-img1"></i> 
      <span>&gt;</span> 
    </a>
    <a href="https://fm.missevan.com/" class="station">
      <i class="home icon-img2"></i> 
      <span>&gt;</span> 
    </a>
    <a href="" class="station">
      <i class="home icon-img3"></i> 
      <span>&gt;</span> 
    </a>
    <span v-show="$root.bLogout" class="logout row" @click="logout();back()"></span>
  </div> 
  
</template>
<script>
import axios from 'axios'
export default {
  name:'user',
  props:['data'],
  computed:{ },
  mounted(){}, 	
  
  beforeMount(){
	  if(window.localStorage.getItem("user")){
      this.$root.user = JSON.parse(window.localStorage.getItem("user")).data
		  
	  }else{
		  this.$root.bNickname1=this.$root.bImg1=true
		  this.$root.bNickname2=this.$root.bImg2=false
		  this.$root.bTab=this.$root.bLogout=false
	  }
  },
  methods:{
    jump(){
      if(window.localStorage.getItem('user')){
       this.$router.push("/")
      }else{
        this.$router.push("/login")
      }
    },
    back(){
        this.$emit("back")
    },
    logout(){
        window.localStorage.removeItem('user');
        this.$router.push('/recommend');
        this.$root.user ={}
        this.$root.bNickname1=this.$root.bImg1=true
        this.$root.bNickname2=this.$root.bImg2=false
        this.$root.bLogout=false
		this.$router.push("/login")
      }

  },
}
   
</script>


