<template>
<div class="reg">
      <div class="form-group">
          <span class="form-title">
              
          </span>
          <input type="text" placeholder="" v-model="username">
          <input type="password" placeholder="" v-model="password" >
          <p class="tip">{{mess}}</p> 
          <button class="reg-btn" @click="reg"></button> 
          <div class="page-bottom">
             
              <span></span>
            <div class="br">
              <p></p>
              <span></span>
              <p></p>
            </div>
          </div>
          <div class="bottom">
              <a></a>
              <router-link to="/login" class="login-back">></router-link>
          </div>
      </div>
      
    </div>
  
</template>

<script>
  export default {
    name:'reg',
    props:{},
    data(){
     return {
         username:'',
         password:'',
         nikename:'',
         mess:''
       }
     },
    components:{},
    mounted(){},
    updated(){},
    methods:{
      reg(){
        this.$axios({
          url:'/api/reg',
          method:'post',
          data:{
            username:this.username,
            password:this.password
          }
        }).then(
          res => {
			  // console.log(res)
            if(res.data.err===0){
              //local  
              this.$router.push('/login')
            }else{
              this.mess = res.data.msg
            }
          }
        )
      }
    }
  }
</script>

