<template>
  <div class="recommend">
    <banner
      :data="banner"
      collectionName="banner"
    />
	<m-sound :Msound="mSound" text1='M' text2='' collectionName='sound'></m-sound>
	<m-sound :Msound="mCartoon" text1='' text2='' collectionName='sound'></m-sound>
	<m-sound :Msound="broadcast" text1='' text2='' collectionName='sound'></m-sound>
	<m-sound :Msound="music" text1='' text2='' collectionName='sound'></m-sound>
	<m-sound :Msound="lover" text1='' text2='' collectionName='sound'></m-sound>
	<m-sound :Msound="ent" text1='' text2='' collectionName='sound'></m-sound>
	<m-sound :Msound="grab" text1='' text2='' collectionName='sound'></m-sound>
	<m-sound :Msound="book" text1='' text2='' collectionName='sound'></m-sound>
	<m-sound :Msound="ring" text1='' text2='' collectionName='sound'></m-sound>
	<m-sound :Msound="hyp" text1='' text2='' collectionName='sound'></m-sound>
  </div>
</template>

<script>
  import banner from '../components/banner/banner.vue'
  import mSound from '../components/m-Sound.vue'
  import {getData} from '../plugins/api.js'

  export default {
    name:'home',
    props:{},
    data(){
      return {
			banner:[],
			mSound:[],
			mCartoon:[],
			broadcast:[],
			music:[],
			lover:[],
			ent:[],
			grab:[],
			book:[],
			ring:[],
			hyp:[]
      }
    },
    components:{banner,mSound},
    mounted(){
      
      this.$axios({
        url:'/api/banner',
        params:{_limit:3,_page:3}
      }).then(
        res => this.banner = res.data.data
      )    
	  // this.$axios({
   //      url:'/api/list',
   //      params:{_limit:3,_page:4}
   //    }).then(
   //      res => this.mSound = res.data.data
   //    )  
	  // this.$axios({
   //      url:'/api/list',
   //      params:{_limit:3,_page:5}
   //    }).then(
   //      res => this.mCartoon = res.data.data
   //    )
		if(window.localStorage.getItem('user')){
			
		getData().then((res)=>{	
			this.mSound=res.data.data.splice(',',3)
			this.mCartoon=res.data.data.splice(',',3)
			this.broadcast=res.data.data.splice(',',3)
			this.music=res.data.data.splice(',',3)
			this.lover=res.data.data.splice(',',3)
			this.ent=res.data.data.splice(',',3)
			this.grab=res.data.data.splice(',',3)
			this.book=res.data.data.splice(',',3)
			this.ring=res.data.data.splice(',',3)
			this.hyp=res.data.data.splice(',',3)
		})
		}
      
      
    },
    updated(){},
    methods:{}
  }
</script>

<style>
  .home{}
  .list{
    padding: 0 0.34rem;
  }
</style>
