<template>
  <div id="app">
    <loading v-show="$root.bLoading"/>
    <my-header @change="change"/>
	<my-tab v-show="$root.bTab"/>
    <router-view/>
	<user :data='num' @back='back'></user>
    
  </div>
</template>

<script>
  import MyHeader from './my-header.vue';
  import User from '../pages/user.vue';
  import myTab from './my-tab.vue'
  import loading from '../components/loading.vue'
export default {
  name: 'App',
  data(){
    return {
		num:'100%' 
    }
  },
  components: {
   loading,MyHeader,User,myTab
  },
  methods:{
    change(data,type){
  
      type ? this.num=data :this.num = '100%'
    },
    back(){
      this.num = '100%'
    },
     checkRootData(path){
        if(/login|reg/.test(path)){
          this.$root.bTab=false
        }else{
          this.$root.bTab=true
        }
      }
  },
   watch:{
    	$route:{
        handler(nextValue){
          let path = nextValue.path;
          this.checkRootData(path)
        },
      }
    }
}
</script>

