<template>
    <div class="login">
        <div class="form-group">
            <span class="form-title">
                 FM
            </span>
            <input type="text" placeholder="" v-model="username">
            <div class="form-row">
                <input type="password" placeholder="" v-model="password">
                <a href="" class="row-right"></a>
            </div>
            <p class="tip">{{mess}}</p>
            <button class="submit-btn btn" @click="login">
                
            </button>
            <router-link to="/reg">
                <button class="reg-btn btn">
                    
            </button>   
            </router-link>
            <div class="page-bottom">
                <span></span>
                <p></p> 
                <span></span>
                <p></p>
            </div>
            <div class="login-bottom">
                <img src="../../public/images/login-bottom.png" alt="">
            </div>
        </div>
    </div>
  
</template>

<script>
  export default {
    name:'login',
    props:{},
    data(){
      return {
        username:'',
        password:'',
        mess:''
      }
    },
    components:{},
    mounted(){},
    updated(){},
    methods:{
      login(){
        this.$axios({
          url:'/api/login',
          method:'post',
          data:{
            username:this.username,
            password:this.password
          }
        }).then(
          res => {
            if(res.data.err===0){
              //local  
              window.localStorage.setItem('user',JSON.stringify(res.data));
			  this.$root.user = res.data.data
			  this.$root.bNickname1=this.$root.bImg1=false
              this.$root.bNickname2=this.$root.bImg2=true
              this.$root.bLogout=true
			  if(!this.$route.query.p){
                this.$router.push('/home')
              }else{
                this.$router.push(this.$route.query.p)
              }
            }else{
              this.mess = res.data.msg
            }
          }
        )
      }
    }
  }
</script>

